<template>
	<view class="containner">
		<watermark></watermark>
		<!-- 顶部导航 -->
		<navTabs :list="list" @getChange="_getChange" @getData="_getData" @clickPrj="_clickPrj" :isTop="'false'"></navTabs>
		<view class="chartsContainer">
			<view class="echartSelect">
				<view class="barSelect">
					<text :class="{active:form.timeUnit===0}" @click="_unitTap(0)">年</text>
					<text :class="{active:form.timeUnit===1}" @click="_unitTap(1)">季</text>
					<text :class="{active:form.timeUnit===2}" @click="_unitTap(2)">月</text>
				</view>
			</view>
			<view class="chartsBox">
				<view class="boxTop">
					<text class="name">金额</text>
					<text>单位：万元</text>
				</view>
				<barDropDown :key='1' :chartData="chartData" :timeUnit="form.timeUnit" :timeSelect="timeSelect"
				 @prevTap="_prevTap(1)" @nextTap="_nextTap(1)" :unitSelect="'false'"></barDropDown>
			</view>
			<view class="chartsBox">
				<view class="boxTop">
					<text class="name">套数</text>
					<text>单位：套</text>
				</view>
				<barDropDown :key='2' :chartData="chartData" :timeUnit="form.timeUnit" :timeSelect="timeSelect"
				 @prevTap="_prevTap(2)" @nextTap="_nextTap(2)" :unitSelect="'false'"></barDropDown>
			</view>
			<view class="express_box chartsBox">
				<view class="boxTop">
					<text class="name">面积</text>
					<text>单位：m²</text>
				</view>
				<view class="echarts_wrap">
					<uni-icons class="icon arrow left" type="arrowleft" size="24" :color="timeSelect.prev?'#03958C':'#DBE4F9'" @click="prevTap"></uni-icons>
					<view class="barEcharts"><barEcharts :Column="chartData"></barEcharts></view>
					<uni-icons class="icon arrow right" type="arrowright" size="24" :color="timeSelect.next?'#03958C':'#DBE4F9'" @click="nextTap"></uni-icons>
				</view>
			</view>
			<view class="express_box chartsBox">
				<view class="boxTop">
					<text class="name">均价</text>
					<text>单位：元/m²</text>
				</view>
				<view class="echarts_wrap">
					<uni-icons class="icon arrow left" type="arrowleft" size="24" :color="timeSelect.prev?'#03958C':'#DBE4F9'" @click="prevTap"></uni-icons>
					<view class="barEcharts"><barEcharts :Column="chartData"></barEcharts></view>
					<uni-icons class="icon arrow right" type="arrowright" size="24" :color="timeSelect.next?'#03958C':'#DBE4F9'" @click="nextTap"></uni-icons>
				</view>
			</view>
		</view>
		<!-- 下面列表 -->
		<view class="express_box">
			<view class="express_list_wrap">
				<view class="express_list" v-for="(item,index) in tdList" :key="index">
					<view class="proIndex">{{index + 1}}</view>
					<view class="right_box">
						<view class="proName">{{item.projectName}}</view>
						<view class="bottom">
							<view class="bottom_item">
								<text>成交金额</text>
								<text>{{item.followUpOverdue}}</text>
								<text class="danwei">万元</text>
							</view>
							<view class="bottom_item">
								<text>成交套数</text>
								<text>{{item.signOverdue}}</text>
								<text class="danwei">套</text>
							</view>
							<view class="bottom_item">
								<text>已售面积</text>
								<text>{{item.subscribeOverdue}}</text>
								<text class="danwei">m²</text>
							</view>
							<view class="bottom_item">
								<text>均价</text>
								<text>{{item.totalOverdue}}</text>
								<text class="danwei">元</text>
							</view>
						</view>
					</view>
				</view>
			</view>
		</view>
		
		<!-- <view class="tabs_item" v-if="current==0">
			<barDropDown :key='0' :chartData="chartData" :unitText="'万元'" :timeUnit="form.timeUnit" :timeSelect="timeSelect"
			 @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></barDropDown>
			<view class="express_box">
				<utable :thList="thList1" :tdList="tdList" :status="7"></utable>
			</view>
		</view>
		<view class="tabs_item" v-if="current==1">
			<barDropDown :key='1' :chartData="chartData" :unitText="'套'" :timeUnit="form.timeUnit" :timeSelect="timeSelect"
			 @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></barDropDown>
			<view class="express_box">
				<utable :thList="thList2" :tdList="tdList" :status="8"></utable>
			</view>
		</view>
		<view class="tabs_item" v-if="current==2">
			<barDropDown :key='2' :chartData="chartData" :unitText="'m²'" :timeUnit="form.timeUnit" :timeSelect="timeSelect"
			 @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></barDropDown>
			<view class="express_box">
				<utable :thList="thList3" :tdList="tdList" :status="1"></utable>
			</view>
		</view>
		<view class="tabs_item" v-if="current==3">
			<barDropDown :key='3' :chartData="chartData" :unitText="'元/m²'" :timeUnit="form.timeUnit" :timeSelect="timeSelect"
			 @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></barDropDown>
			<view class="express_box">
				<utable :thList="thList4" :tdList="tdList" :status="9"></utable>
			</view>
		</view> -->
		<tabBar></tabBar>
	</view>
</template>

<script>
	import barEcharts from '@/components/echartsComponent/barEcharts.vue';
	import watermark from '@/components/watermark.vue';
	import tabBar from '@/components/tabBar.vue';
	import navTabs from '@/components/nav-tabs.vue';
	import barDropDown from '@/components/barDropDown.vue';
	import utable from '@/components/utable.vue';	
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	import { bus } from '@/utils/bus.js';
	export default {
		data() {
			return {
				current: 0,
				list: [{
					name: '金额'
				}, {
					name: '套数'
				}, {
					name: '面积'
				}, {
					name: '均价'
				}],
				
				form: {
					companyId:'', // 公司id
					projectId:'', // 项目id
					tabCode: '', // tabs 0金额 1套数 2面积 3均价
					timeUnit: 2, // 时间统计 0年 1季 2月 3周
					time: '', // 当前统计格式 prev/next值
				},
				timeSelect: { // 柱状图选择
					prev: '',
					next: '',
				},
				etype: 0, //0第一次加载 1查询 2下拉刷新 3上拉加载更多
				page: {
					pageNum: 1,
					pageSize: 10
				},
				totalPages: 0, // 总页数
				loading: false,
				// 柱状图
				chartData: {
					categories: [],
					series: [{
						data: []
					}],
				},
				thList1:[{name:'序号'},{name:'项目'},{name:'成交金额'}],
				thList2:[{name:'序号'},{name:'项目'},{name:'成交套数'}],
				thList3:[{name:'序号'},{name:'项目'},{name:'已售'},{name:'待售'}],
				thList4:[{name:'序号'},{name:'项目'},{name:'均价'}],
				tdList:[], // 表格数据
			}
		},
		methods: {
			// 获取列表
			getList() {
				let that = this
				that.form.tabCode = that.current
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('3006', Object.assign({}, that.form, that.page), function(res) {
					Tools.hideLoading()
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						that.timeSelect.prev = res.data.data.histogramData.pre
						that.timeSelect.next = res.data.data.histogramData.next
						that.chartData.series[0].data = res.data.data.histogramData.yData
						that.$set(that.chartData, 'series', that.chartData.series)
						that.$set(that.chartData, 'categories', res.data.data.histogramData.xData)
						that.tdList = that.etype == 3 ? that.tdList.concat(res.data.data.tableData.list) : res.data.data.tableData.list
						that.totalPages = res.data.data.tableData.totalPage
						if (that.loading) {
							that.loading = false
							uni.stopPullDownRefresh()
						}
						bus.$emit('changeEcharts',that.chartData)
					}
				})
			},
			// 点击公司回调
			_getData(val, val2){
				this.form.companyId = val
				this.form.projectId = val2
				this.page.pageNum = 1
				this.tdList = []
				this.getList()
			},
			// 点击项目回调
			_clickPrj(val){
				this.form.projectId = val
				this.page.pageNum = 1
				this.tdList = []
				this.getList()
			},
			// 下拉刷新
			onPullDownRefresh() {
				this.page.pageNum = 1
				this.etype = 2
				if (!this.loading) {
					this.loading = true
					this.getList()
				}
			},
			// 上拉加载
			onReachBottom() {
				if (this.totalPages > this.page.pageNum) {
					this.etype = 3
					this.page.pageNum++
					this.flag = true
					this.getList()
				}
			},
			// tabs切换
			// _getChange(index) {
			// 	this.current = index
			// 	this.page.pageNum = 1
			// 	this.tdList = []
			// 	this.form.time = ''
			// 	this.getList()
			// },
			// 柱状图左翻
			_prevTap(index) {
				this.form.time = this.timeSelect.prev
				this.etype = 0
				this.page.pageNum = 1
				this.getList()
			},
			// 柱状图右翻
			_nextTap(index) {
				this.form.time = this.timeSelect.next
				this.etype = 0
				this.page.pageNum = 1
				this.getList()
			},
			// 柱状图单位选择
			_unitTap(index) {
				this.form.timeUnit = index
				this.form.time = ''
				this.etype = 0
				this.page.pageNum = 1
				this.getList()
			}
		},
		components: {
			barEcharts,
			watermark,
			tabBar,
			navTabs,
			barDropDown,
			utable
		},
	}
</script>

<style lang="scss">
	page {
		background-color: #f6f6f6;
	}
	.chartsContainer{
		padding-top: 80rpx;
		.echartSelect{
			display: flex;
			justify-content: flex-end;
			padding: 30rpx 30rpx 10rpx;
			background-color: #fff;
			color: #fff;
			.barSelect{
				height: 48rpx;
				background-color: #03958C;
				border-radius: 24rpx;
				display: flex;
				align-items: center;
				padding: 0 20rpx;
				text{
					width: 60rpx;
					height: 40rpx;
					line-height: 40rpx;
					border-radius: 8rpx;
					display: inline-block;
					text-align: center;
				}
				.active{
					color: #F5B00C;
				}
			}
		}
		.express_box{
			margin-top: 0;
		}
		.chartsBox{
			border-bottom: 2rpx solid #e6e6e6;
			background-color: #fff;
			.boxTop{
				display: flex;
				justify-content: space-between;
				align-items: center;
				font-size: 24rpx;
				padding: 30rpx 30rpx 0;
				.name{
					font-size: 36rpx;
				}
			}
		}
	}
	
</style>
